"use client";
import { useRouter } from 'next/navigation';
import { useState } from 'react';
import { motion } from 'framer-motion';

const GENRES = [
  { key: 'Military Sci-Fi', sub: 'Tactics • Hardware • Grit' },
  { key: 'Space Opera', sub: 'Epic • Mythic • Grand' },
  { key: 'Space Tech Thriller', sub: 'Hard • Systems • Intrigue' },
];

export default function Home() {
  const router = useRouter();
  const [loading, setLoading] = useState<string | null>(null);

  async function start(genre: string) {
    setLoading(genre);
    try {
      const res = await fetch('/api/story/start', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify({ genre }) });
      const json = await res.json();
      if (json.sessionId) router.push(`/play/${json.sessionId}`);
    } finally {
      setLoading(null);
    }
  }

  return (
    <div className="px-4 sm:px-6 py-12">
      <div className="max-w-3xl mx-auto text-center mb-10">
        <motion.h1 initial={{ opacity: 0, y: 8 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.5 }} className="text-3xl sm:text-4xl font-semibold tracking-tight">Choose Your Universe</motion.h1>
        <motion.p initial={{ opacity: 0 }} animate={{ opacity: 1 }} transition={{ delay: 0.15 }} className="mt-3 opacity-70">Minimal, cinematic, AI‑driven adventures.</motion.p>
      </div>
      <div className="grid gap-4 sm:grid-cols-3 max-w-4xl mx-auto">
        {GENRES.map((g, i) => (
          <motion.button
            key={g.key}
            initial={{ opacity: 0, y: 12 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.05 * i }}
            onClick={() => start(g.key)}
            className="group relative overflow-hidden rounded-2xl p-[2px] bg-gradient-to-br from-cyan-400/60 via-fuchsia-400/40 to-violet-500/60 hover:from-cyan-400 hover:to-violet-500 transition"
          >
            <div className="rounded-[14px] bg-white/80 dark:bg-black/40 backdrop-blur-md p-6 min-h-[140px] grid">
              <div className="self-center text-center">
                <div className="text-lg font-medium">{g.key}</div>
                <div className="text-xs opacity-60 mt-1">{g.sub}</div>
              </div>
              <div className="absolute inset-0 -z-10 opacity-0 group-hover:opacity-100 transition bg-[radial-gradient(120px_60px_at_50%_0%,rgba(255,255,255,0.14),transparent_60%)]" />
              {loading === g.key && <div className="absolute inset-0 grid place-items-center bg-black/5 dark:bg-white/5 text-xs">Starting…</div>}
            </div>
          </motion.button>
        ))}
      </div>
    </div>
  );
}
